import appOrderGoodsInfo from '../../../components/page-component/app-order-goods-info/app-order-goods-info.vue';


export default {
    components: {
        appOrderGoodsInfo,
    },
    data() {
        return {
            orderDetail: {},
            id: null,
            isShow: false,
        }
    },
    methods: {
        getRefundDetail() {
            let self = this;
            self.$showLoading();
            self.$request({
                url: this.$api.order.apply_refund,
                data: {
                    id: self.id,
                }
            }).then(response => {
                self.$hideLoading();
                self.isShow = true;
                if (response.code === 0) {
                    self.orderDetail = response.data.detail;
                } else {
                    uni.showModal({
                        title: '',
                        content: response.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                self.$hideLoading();
            });
        },
        jump(type) {
            let url = '';
            if (type === 1) {
                url = '/pages/order/refund/refund?id=' + this.id + '&type=3';
            } else if (type === 2) {
                url = '/pages/order/refund/refund?id=' + this.id + '&type=1';
            } else {
                url = '/pages/order/refund/refund?id=' + this.id + '&type=2';
            }

            uni.navigateTo({
                url: url
            });
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.id = options.id;
        this.getRefundDetail();
    }
}
